import * as React from "react";

import { useContext, useEffect, useRef } from "react";
import { ImageContext } from "../../context/ImageContext";
import { fontLineHeight } from "../../data/fonts";

// import snow from "../../assets/images/snow.png";
// import fabric from "../../assets/images/fabric.png";
// import leaves from "../../assets/images/leaves.png";
// import { image } from "html2canvas/dist/types/css/types/image";

type Props = {
  setIsOverflow: React.Dispatch<React.SetStateAction<any>>;
};

const ImageArea: React.FC<Props> = ({ setIsOverflow }) => {
  const textRef = useRef<HTMLDivElement>(null);

  const imageContext = useContext(ImageContext);
  let [height, width] = imageContext.imageState.imageSize.split("x");
  var style: React.CSSProperties = {
    margin: "auto",
    padding: "30px",
    lineHeight: fontLineHeight[imageContext.imageState.font] ?? "15px",
    // Padding*2 minus
    height: Number(height) / 2 - 60,
    width: Number(width) / 2 - 60,
    color: imageContext.imageState.fontColor,
    fontSize: imageContext.imageState.fontSize,
    fontFamily: imageContext.imageState.font,
    whiteSpace: "pre-line",
    display: "flex",
    justifyContent: "center",
    alignContent: "center",
    flexDirection: "column",
    textAlign: "justify",
    overflow: "hidden",
    // textAlignLast: "center",
  };
  if (imageContext.imageState.backgroundTexture === "") {
    style = {
      ...style,
      backgroundColor: imageContext.imageState.backgroundColor,
    };
  } else {
    style = {
      ...style,
      backgroundImage: `url(${require("../../assets/images/" +
        imageContext.imageState.backgroundTexture +
        ".png")})`,
      backgroundRepeat: "repeat",
    };
  }
  useEffect(() => {
    if (textRef && textRef.current) {
      if (
        textRef.current.offsetHeight < textRef.current.scrollHeight ||
        textRef.current.offsetWidth < textRef.current.scrollWidth
      ) {
        setIsOverflow(true);
        return;
      }
    }

    setIsOverflow(false);
  }, [imageContext.imageState, setIsOverflow]);
  return (
    <div className="image-area" style={style} id="image-area" ref={textRef}>
      {imageContext.imageState.text}
    </div>
  );
};

export default ImageArea;
