import { createContext, useState } from "react";

export interface ImageState {
  text: string;
  imageSize: string;
  font: string;
  fontSize: number;
  fontColor: string;
  backgroundColor: string;
  backgroundTexture: string;
}

const defaultImageState: ImageState = {
  text: "",
  imageSize: "1080x800",
  font: "Libre Baskerville",
  fontSize: 20,
  fontColor: "#000",
  backgroundColor: "#FFF",
  backgroundTexture: "",
};

interface ImageProviderProps {
  children: React.ReactNode;
}

export interface ImageContextProps {
  imageState: ImageState;
  setImageState: (imageInfo: ImageState) => void;
  updateText: (text: string) => void;
  updateImageSize: (size: string) => void;
  updateFont: (font: string) => void;
  updateFontSize: (font: number) => void;
  updateFontColor: (fontColor: string) => void;
  updateBackgroundColor: (backgroundColor: string) => void;
  updateBackgroundTexture: (texture: string) => void;
}

const ImageContext = createContext<ImageContextProps>({
  imageState: defaultImageState,
  setImageState: (defaultImageState) => {},
  updateImageSize: () => {},
  updateText: () => {},
  updateFont: () => {},
  updateFontSize: () => {},
  updateFontColor: () => {},
  updateBackgroundColor: () => {},
  updateBackgroundTexture: () => {},
});
const { Provider } = ImageContext;

const ImageProvider = ({ children }: ImageProviderProps) => {
  const [imageState, setImageState] = useState<ImageState>(defaultImageState);

  //
  const setImageInfo = ({
    text,
    imageSize,
    font,
    fontSize,
    fontColor,
    backgroundColor,
    backgroundTexture,
  }: ImageState) => {
    setImageState({
      text,
      imageSize,
      font,
      fontSize,
      fontColor,
      backgroundColor,
      backgroundTexture,
    });
  };

  const updateText = (text: string) => {
    setImageState({ ...imageState, text: text });
  };
  const updateImageSize = (size: string) => {
    setImageState({ ...imageState, imageSize: size });
  };
  const updateFont = (font: string) => {
    setImageState({ ...imageState, font: font });
  };

  const updateFontSize = (fontSize: number) => {
    setImageState({ ...imageState, fontSize: fontSize });
  };
  const updateFontColor = (fontColor: string) => {
    setImageState({ ...imageState, fontColor: fontColor });
  };
  const updateBackgroundColor = (backgroundColor: string) => {
    setImageState({
      ...imageState,
      backgroundColor: backgroundColor,
      backgroundTexture: "",
    });
  };
  const updateBackgroundTexture = (texture: string) => {
    setImageState({
      ...imageState,
      backgroundTexture: texture,
    });
  };
  return (
    <Provider
      value={{
        imageState,
        setImageState: (imageInfo: ImageState) => setImageInfo(imageInfo),
        updateText,
        updateImageSize,
        updateFont,
        updateFontSize,
        updateFontColor,
        updateBackgroundColor,
        updateBackgroundTexture,
      }}
    >
      {children}
    </Provider>
  );
};
export { ImageContext, ImageProvider };
