import {
  Box,
  Button,
  FormControl,
  InputLabel,
  ListItemIcon,
  ListItemText,
  MenuItem,
  Popover,
  Select,
  Slider,
  Stack,
  TextField,
  Typography,
} from "@mui/material";
import RefreshIcon from "@mui/icons-material/Refresh";
import React, { useCallback, useContext } from "react";
import ColorPicker from "../ColorPicker";
import { ImageContext } from "../../context/ImageContext";
import { DownloadOutlined, ListAlt } from "@mui/icons-material";
import html2canvas from "html2canvas";
import downloadjs from "downloadjs";

import snow from "../../assets/thumbnails/snow.png";
import fabric from "../../assets/thumbnails/fabric.png";
import leaves from "../../assets/thumbnails/leaves.png";
import book from "../../assets/thumbnails/leaves.png";
import heart from "../../assets/thumbnails/heart.png";
import grey from "../../assets/thumbnails/grey.png";
import diagmond from "../../assets/thumbnails/diagmond.png";
import { fonts } from "../../data/fonts";
import { samples } from "../../data/samples";

type Props = {
  isOverflow: boolean;
};
const ImageForm: React.FC<Props> = ({ isOverflow }) => {
  const imageContext = useContext(ImageContext);

  const [anchorEl, setAnchorEl] = React.useState<HTMLButtonElement | null>(
    null
  );

  const handleClick = (event: React.MouseEvent<HTMLButtonElement>) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  const open = Boolean(anchorEl);
  const id = open ? "simple-popover" : undefined;

  const handleCaptureClick = useCallback(async () => {
    const pricingTableElmt = document.querySelector<HTMLElement>(".image-area");
    if (!pricingTableElmt) return;

    const canvas = await html2canvas(pricingTableElmt, {
      scale: 2,
    });
    const dataURL = canvas.toDataURL("image/png");
    downloadjs(dataURL, "post.png", "image/png");
  }, []);
  return (
    <div>
      <Stack spacing={2}>
        <Stack direction={"row"} spacing={2}>
          <Button
            variant="outlined"
            startIcon={<DownloadOutlined />}
            onClick={handleCaptureClick}
          >
            Download PNG
          </Button>
          <div>
            <Button
              startIcon={<ListAlt />}
              aria-describedby={id}
              variant="contained"
              onClick={handleClick}
            >
              Samples
            </Button>
            <Popover
              id={id}
              open={open}
              anchorEl={anchorEl}
              onClose={handleClose}
              anchorOrigin={{
                vertical: "bottom",
                horizontal: "left",
              }}
            >
              {Object.keys(samples).map((item) => {
                return (
                  <MenuItem
                    value={item}
                    onClick={(event) => {
                      imageContext.setImageState(samples[item]);
                    }}
                  >
                    {item}
                  </MenuItem>
                );
              })}
            </Popover>
          </div>
        </Stack>
        <TextField
          inputProps={isOverflow ? { style: { color: "#d32f2f" } } : {}}
          id="outlined-multiline-static"
          label="Text"
          placeholder="Enter text here"
          multiline
          rows={5}
          value={imageContext.imageState.text}
          defaultValue=""
          onChange={(event) => {
            imageContext.updateText(event.target.value);
          }}
          helperText={
            isOverflow
              ? "Reduce the font size or delete some text to fit it in box"
              : ""
          }
          error={isOverflow}
        />
        <FormControl fullWidth>
          <InputLabel id="demo-simple-select-label">Image Size</InputLabel>
          <Select
            labelId="demo-simple-select-label"
            id="demo-simple-select"
            label="Image Size"
            onChange={(event) => {
              imageContext.updateImageSize(event.target.value as string);
            }}
            value={imageContext.imageState.imageSize}
          >
            <MenuItem value={"1080x800"}>Potrait (1080 x 800)</MenuItem>
            <MenuItem value={"1080x1350"}>Landscape (1080 x 1350)</MenuItem>
            <MenuItem value={"1080x1080"}>Square (1080 x 1080)</MenuItem>
          </Select>
        </FormControl>
        <FormControl fullWidth>
          <InputLabel id="demo-simple-select-label">Font</InputLabel>
          <Select
            labelId="demo-simple-select-label"
            id="demo-simple-select"
            label="Font"
            value={imageContext.imageState.font}
            onChange={(event) => {
              imageContext.updateFont(event.target.value as string);
            }}
          >
            {fonts.map((f) => (
              <MenuItem value={f} style={{ fontFamily: f }}>
                {f}
              </MenuItem>
            ))}
          </Select>
        </FormControl>
        <Stack spacing={2} direction="row" sx={{ mb: 1 }} alignItems="center">
          <label>Font Size:</label>
          <Box sx={{ width: 360 }}>
            <Slider
              aria-label="font_size"
              value={imageContext.imageState.fontSize}
              min={5}
              max={50}
              onChange={(event) => {
                imageContext.updateFontSize(
                  (event?.target as HTMLInputElement).value as unknown as number
                );
              }}
            />
          </Box>
          <label>{imageContext.imageState.fontSize}</label>
        </Stack>
        <ColorPicker
          label="Font Color"
          color={imageContext.imageState.fontColor as string}
          changeHandler={(color) => {
            imageContext.updateFontColor(color);
          }}
        />
        <Stack direction={"row"} spacing={2} style={{ alignItems: "center" }}>
          <ColorPicker
            label="Background Color"
            color={imageContext.imageState.backgroundColor as string}
            changeHandler={(color) => {
              imageContext.updateBackgroundColor(color);
            }}
          />
          <span>OR</span>
          <FormControl style={{ width: "200px" }}>
            <InputLabel id="texture-select-label">
              Background Texture
            </InputLabel>
            <Select
              labelId="texture-select-label"
              id="demo-simple-select"
              label="Background Texture"
              onChange={(event) => {
                imageContext.updateBackgroundTexture(
                  event.target.value as string
                );
              }}
              value={imageContext.imageState.backgroundTexture}
            >
              <MenuItem value="">
                <em>None</em>
              </MenuItem>
              <MenuItem value={"book"}>
                <ListItemIcon>
                  <img
                    alt=""
                    src={book}
                    width="20px"
                    style={{ border: "1px grey solid" }}
                  />
                </ListItemIcon>
                <ListItemText
                  primary="Book"
                  style={{ display: "inline-block", verticalAlign: "bottom" }}
                />
              </MenuItem>
              <MenuItem value={"fabric"}>
                <ListItemIcon style={{ display: "inline-block" }}>
                  <img
                    alt=""
                    src={fabric}
                    width="20px"
                    style={{ border: "1px grey solid" }}
                  />
                </ListItemIcon>
                <ListItemText
                  primary="Fabric"
                  style={{ display: "inline-block", verticalAlign: "bottom" }}
                />
              </MenuItem>
              <MenuItem value={"leaves"}>
                <ListItemIcon>
                  <img
                    alt=""
                    src={leaves}
                    width="20px"
                    style={{ border: "1px grey solid" }}
                  />
                </ListItemIcon>
                <ListItemText
                  primary="Leaves"
                  style={{ display: "inline-block", verticalAlign: "bottom" }}
                />
              </MenuItem>
              <MenuItem value={"snow"}>
                <ListItemIcon>
                  <img
                    alt=""
                    src={snow}
                    width="20px"
                    style={{ border: "1px grey solid" }}
                  />
                </ListItemIcon>
                <ListItemText
                  primary="Snow"
                  style={{ display: "inline-block", verticalAlign: "bottom" }}
                />
              </MenuItem>
              <MenuItem value={"heart"}>
                <ListItemIcon>
                  <img
                    alt=""
                    src={heart}
                    width="20px"
                    style={{ border: "1px grey solid" }}
                  />
                </ListItemIcon>
                <ListItemText
                  primary="Heart"
                  style={{ display: "inline-block", verticalAlign: "bottom" }}
                />
              </MenuItem>
              <MenuItem value={"grey"}>
                <ListItemIcon>
                  <img
                    alt=""
                    src={grey}
                    width="20px"
                    style={{ border: "1px grey solid" }}
                  />
                </ListItemIcon>
                <ListItemText
                  primary="Grey"
                  style={{ display: "inline-block", verticalAlign: "bottom" }}
                />
              </MenuItem>
              <MenuItem value={"diagmond"}>
                <ListItemIcon>
                  <img
                    alt=""
                    src={diagmond}
                    width="20px"
                    style={{ border: "1px grey solid" }}
                  />
                </ListItemIcon>
                <ListItemText
                  primary="Diagmond"
                  style={{ display: "inline-block", verticalAlign: "bottom" }}
                />
              </MenuItem>
            </Select>
          </FormControl>
        </Stack>
      </Stack>
    </div>
  );
};

export default ImageForm;
